import React, { useState, useCallback } from 'react';
import ColorPicker from 'material-ui-color-picker';

import TextField from '@material-ui/core/TextField';
import { DropzoneArea } from 'material-ui-dropzone';
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/styles';
import AppBar from '@material-ui/core/AppBar';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Modal from '@material-ui/core/Modal';
import Tabs from '@material-ui/core/Tabs';
import Grid from '@material-ui/core/Grid';
import Fade from '@material-ui/core/Fade';
import Tab from '@material-ui/core/Tab';
import Fab from '@material-ui/core/Fab';

import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';

import CardItem from '../components/Card';

import './index.scss';

const defaultSlider = {
  text: '',
  link: '',
  isSlide: false,
  color: '',
  image: '',
};

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  margin: {
    marginTop: theme.spacing(4),
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  dropzoneContainer: {
    '& .MuiGrid-container': {
      alignItems: 'center',
      justifyContent: 'center',
    }
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(0),
    right: theme.spacing(2),
  },
  modal: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  paper: {
    backgroundColor: theme.palette.background.paper,
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
  },
}));

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Paper
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && children}
    </Paper>
  );
}

function a11yProps(index) {
  return {
    id: `tab-${index}`,
    'aria-controls': `tabpanel-${index}`,
  };
}

function Detailed(props) {
  const classes = useStyles();
  const { state: { sliders }, actions: {
    addSlider,
    updateSlider,
    deleteSlider,
  } } = useStore();

  const [slider, setSlider] = useState({ ...defaultSlider });
  const [errors, setErrors] = useState({});
  const [tab, setTab] = useState(0);
  const [open, setOpen] = useState(false);

  const handleSelect = useCallback(item => {
    setSlider(item);
    setOpen(true);
  }, []);

  const handleChange = useCallback((key, value) => {
    setSlider(data => ({ ...data, [key]: value }));
  }, []);

  const handleDelete = useCallback(id => {
    deleteSlider(id);
  }, [deleteSlider]);

  const handleSave = useCallback(() => {
    let error = false;

    if (!slider.image) {
      setErrors({
        image: !slider.image,
      });
      error = true;
    }

    if (slider._id) {
      !error && updateSlider({ ...slider, isSlide: tab === 1}).then(() => {
        setSlider({ ...defaultSlider });
        setOpen(false);
      });
    } else {
      if (!error) {
        addSlider({ ...slider, isSlide: tab === 1 }).then(() => {
          setSlider({ ...defaultSlider });
          setOpen(false);
        });
      }
    }
  }, [updateSlider, addSlider, slider, tab]);

  return (
    <div className={classes.root}>
      <AppBar position="static"  color="default">
        <Tabs
          variant="scrollable"
          scrollButtons="auto"
          value={tab}
          onChange={(e, newTab) => setTab(newTab)}
          aria-label="tabs"
        >
          <Tab fullWidth label='Main Slider' {...a11yProps(0)}/>
          <Tab fullWidth label='Service Slider' {...a11yProps(1)} />
        </Tabs>
        <Fab onClick={() => setOpen(true)} className={classes.fab} color='primary' variant='outlined'> <AddIcon/> </Fab>
      </AppBar>
      <TabPanel value={tab} index={0} style={{ padding: 15 }}>
        <Grid
          container
          spacing={2}
        >
          {sliders.filter(item => !item.isSlide).map(item => (
            <Grid item xs={12} md={4} lg={4} key={item._id}>
              <CardItem
                onDelete={() => handleDelete(item._id)}
                onEdit={() => handleSelect(item)}
                title={item.text}
                description={item.link}
                image={`/${item.image}`}
              />
            </Grid>
          ))}
        </Grid>
      </TabPanel>
      <TabPanel value={tab} index={1} style={{ padding: 15 }}>
        <Grid
          container
          spacing={2}
        >
          {sliders.filter(item => item.isSlide).map(item => (
            <Grid item xs={12} md={4} lg={4} key={item._id}>
              <CardItem
                onDelete={() => handleDelete(item._id)}
                onEdit={() => handleSelect(item)}
                title={item.text}
                description={item.link}
                image={`/${item.image}`}
              />
            </Grid>
          ))}
        </Grid>
      </TabPanel>
      <Modal
        aria-labelledby="transition-modal-title"
        aria-describedby="transition-modal-description"
        className={classes.modal}
        open={open}
        onClose={() => {
          setOpen(false);
          setSlider({ ...defaultSlider });
        }}
        closeAfterTransition
        BackdropComponent={Backdrop}
        BackdropProps={{
          timeout: 500,
        }}
      >
        <Fade in={open}>
          <div className={classes.paper}>
            <Grid container spacing={2}>
              <Grid
                className={classes.margin}
                container
                spacing={2}
              >
                <Grid item xs={12} md={6} lg={6}>
                  <Grid container spacing={6}>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Text"
                        onChange={e => handleChange('text', e.target.value)}
                        value={slider.text}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <TextField
                        fullWidth
                        label="Link"
                        onChange={e => handleChange('link', e.target.value)}
                        value={slider.link}
                      />
                    </Grid>
                    <Grid item xs={12}>
                      <ColorPicker
                        fullWidth
                        label='Text color'
                        value={slider.color}
                        TextFieldProps={{ value: slider.color }}
                        onChange={color => handleChange('color', color)}
                      />
                    </Grid>
                  </Grid>
                </Grid>
                <Grid item xs={12} md={6} lg={6}>
                  <Grid container spacing={2}>
                    <Grid item xs={12} md={8} lg={8}>
                      <DropzoneArea
                        key={slider.image}
                        dropzoneClass={classes.dropzoneContainer}
                        showPreviewsInDropzone
                        onChange={e => handleChange('image', e[0])}
                        acceptedFiles={['image/*']}
                        initialFiles={slider.image ? [`/${slider.image}`] : []}
                        filesLimit={1}
                      />
                    </Grid>
                    {slider.image && typeof slider.image === 'string' && <Grid item xs={12} md={4} lg={4}>
                      <img style={{ maxHeight: 150 }} alt='Uploaded image' src={`/${slider.image}`} />s
                    </Grid>}
                  </Grid>
                </Grid>
                <Grid item style={{ marginTop: 16 }}>
                  <Button
                    variant="contained"
                    onClick={() => {
                      setOpen(false);
                      setSlider({ ...defaultSlider });
                    }}
                  >
                    Cancel
                  </Button>
                </Grid>
                <Grid item style={{ marginTop: 16 }}>
                  <Button
                    variant="contained"
                    color="primary"
                    onClick={handleSave}
                  >
                    Submit
                  </Button>
                </Grid>
              </Grid>
            </Grid>
          </div>
        </Fade>
      </Modal>
    </div>
  );
}

export default Detailed;
