import React, { useEffect, useState } from 'react';
import { ContentState, convertFromHTML, convertToRaw, EditorState } from 'draft-js';
import { Editor as DraftEditor } from 'react-draft-wysiwyg';
import draftToHtml from 'draftjs-to-html';

import { makeStyles } from '@material-ui/styles';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import AppBar from '@material-ui/core/AppBar';
import Paper from '@material-ui/core/Paper';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';

import SaveIcon from '@material-ui/icons/Save';

import { useStore } from '../../store/DataProvider';

import { defaultToolbarConfig } from '../../configs/configToolbar';
import { strings } from '../../configs/strings';

import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4),
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  dropzoneContainer: {
    '& .MuiGrid-container': {
      alignItems: 'center',
      justifyContent: 'center',
    }
  },
  fabButton: {
    position: 'absolute',
    zIndex: 1,
    right: 6,
    top: 6,
  },
}));

const getEditorValue = (data) => {
  const state = data ? ContentState.createFromBlockArray(
    convertFromHTML(data).contentBlocks,
    convertFromHTML(data).entityMap
  ) : '';

  if (state) {
    return EditorState.createWithContent(state);
  } else {
    return EditorState.createEmpty();
  }
};

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <Paper
      role="tabpanel"
      hidden={value !== index}
      id={`tabpanel-${index}`}
      aria-labelledby={`tab-${index}`}
      {...other}
    >
      {value === index && children}
    </Paper>
  );
}

function a11yProps(index) {
  return {
    id: `tab-${index}`,
    'aria-controls': `tabpanel-${index}`,
  };
}

function Dashboard() {
  const classes = useStyles();
  const { state: { texts }, actions: { updateTexts }} = useStore();

  const [pages, setPages] = useState(() => {
    const data = {};
    strings.forEach(page => {
      page.strings.forEach(field => {
        if (field.type === 'editor') {
          data[field.key] = getEditorValue('');
        } else {
          data[field.key] = '';
        }
      });
    });

    return data;
  });
  const [tab, setTab] = useState(0);

  const handleTextFieldChange = (key, value) => {
    setPages(prev => ({ ...prev, [key]: value }));
  };

  const handleSave = () => {
    const data = [];

    for (const key in pages) {
      if (pages.hasOwnProperty(key)) {
        if (typeof pages[key] === 'object') {
          data.push({ key, text: draftToHtml(convertToRaw(pages[key].getCurrentContent())) })
        } else {
          data.push({ key, text: pages[key] });
        }
      }
    }

    updateTexts(data);
  };

  useEffect(() => {
      const data = { ...pages };
      texts.forEach(text => {
        let field = {};
        strings.forEach(item => {
          field = item.strings.find(str => str.key === text.key) || field;
        });


          if (field.type === 'editor') {
            data[text.key] = getEditorValue(text.text);
          } else {
            data[text.key] = text.text;
          }
      });

      setPages(data);
  }, [texts.__v]);

  return (
    <div className={classes.root}>

      <AppBar position="static"  color="default">
        <Tabs variant="scrollable" scrollButtons="auto" value={tab} onChange={(e, newTab) => setTab(newTab)} aria-label="tabs">
          {strings.map((data, index) => (
            <Tab key={index} label={data.page} {...a11yProps(index)} />
          ))}
        </Tabs>
        <Button onClick={handleSave}  color="secondary" variant="contained" className={classes.fabButton}>
          <SaveIcon /> Save
        </Button>
      </AppBar>
      {strings.map((data, i) => (
        <TabPanel value={tab} key={i} index={i} style={{ padding: 15 }}>
          {data.strings.map((field, index) => {
            if (field.type === 'editor') {
              return (
                <span key={index} style={{ marginTop: 15 }}>
                  <Grid item xs={12}>
                    <Typography variant='subtitle1'>{field.label}</Typography>
                  </Grid>
                  <Grid item xs={12}>
                    <DraftEditor
                      wrapperStyle={{
                        width: '100%',
                        minHeight: 150,
                        overflow: 'scroll',
                        padding: 5,
                        border: false ? '1px solid red' : '1px solid rgba(0, 0, 0, 0.42)'
                      }}
                      editorState={pages[field.key]}
                      onEditorStateChange={editorState => handleTextFieldChange(field.key, editorState)}
                      toolbar={defaultToolbarConfig}
                    />
                  </Grid>
                </span>
              );
            }
            return (
              <Grid item xs={12} key={index} style={{ marginTop: 15 }}>
                <TextField
                  fullWidth
                  value={pages[field.key]}
                  onChange={e => handleTextFieldChange(field.key, e.target.value)}
                  label={<Typography variant='subtitle1'>{field.label}</Typography>}
                />
              </Grid>
            );
          })}
        </TabPanel>
      ))}
    </div>
  );
}

export default Dashboard;
