import React, {useCallback, useState} from 'react';
import { makeStyles } from '@material-ui/styles';

import CircularProgress from '@material-ui/core/CircularProgress';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';

import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';

import CardItem from '../components/Card';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
  loading: {
    display: 'flex',
    height: '100%',
    flexGrow: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
}));

function Services(props) {
  const classes = useStyles();
  const { state: { services }, actions: { deleteService } } = useStore();

  const [loading, setLoading] = useState(false);

  const handleDelete = useCallback(id => {
    setLoading(true);
    deleteService(id).then(() => setLoading(false));
  }, [deleteService]);

  if (loading) {
    return (
      <div className={classes.loading}>
        <CircularProgress />
      </div>
    );
  }

  return (
    <div className={classes.root}>
      <Grid
        container
        spacing={2}
      >
        {services.map(item => {
          return (
            <Grid
              key={item._id}
              item
              xs={12}
              sm={6}
              md={4}
              lg={4}
              xl={3}
            >
              <CardItem
                onDelete={() => handleDelete(item._id)}
                onEdit={() => props.history.push(`services/detailed/${item._id}`)}
                title={item.title}
                description={item.description}
                image={`/${item.image}`}
                points={item.points}
                date={item.date}
              />
            </Grid>
          );
        })}
      </Grid>
      <Fab aria-label="Edit" className={classes.fab} color="secondary" onClick={() => props.history.push(`services/detailed`)}>
        <AddIcon />
      </Fab>
    </div>
  );
}

export default Services;
