import React, { useState, useCallback } from 'react';
import { makeStyles } from '@material-ui/styles';
import MaterialTable from 'material-table';

import Checkbox from '@material-ui/core/Checkbox';
import Grid from '@material-ui/core/Grid';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExportIcon from '@material-ui/icons/CloudDownload';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import LastPageIcon from '@material-ui/icons/LastPage';
import CancelIcon from '@material-ui/icons/Cancel';
import SearchIcon from '@material-ui/icons/Search';
import CheckIcon from '@material-ui/icons/Check';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
}));

function Configs() {
  const classes = useStyles();
  const {
    state: {
      prices,
      categories
    },
    actions: {
      addCategory,
      updateCategory,
      deleteCategory,
      addPrice,
      updatePrice,
      deletePrice
    }
  } = useStore();

  const [open, setOpen] = useState(false);
  const [loading, setLoading] = useState(false);

  const handleClose = useCallback(() => setOpen(false), []);

  const handleAdd = useCallback(data => {
    return addPrice(data);
  }, [addPrice]);

  const handleDelete = useCallback(id => {
    return deletePrice(id);
  }, [deletePrice]);

  const handleUpdate = useCallback((newData) => {
    const { title, price, duration, categoryId, order } = newData;

    if (!title || !price || !duration || !categoryId || order === '') {
      return new Promise((res, rej) => rej());
    } else {
      return updatePrice(newData);
    }
  }, [updatePrice]);

  const categoriesOptions = {};

  categories.forEach(cat => categoriesOptions[cat._id] = cat.title);

  const columns = [
    {
      title: 'Title',
      field: 'title',
    },
    {
      title: 'Order',
      field: 'order',
      type: 'numeric',
    },
    {
      title: 'Price',
      field: 'price',
      type: 'numeric',
    },
    {
      title: 'Sale',
      field: 'sale',
      editComponent: props => {
        return (
          <Checkbox
            value={props.value}
            onChange={(e, checked) => props.onChange(checked)}
            helperText={props.sale ? "Incorrect entry" : ""}
          />
        )
      },
      render: (rowData) => <Checkbox checked={rowData.sale} disabled />
    },
    {
      title: 'Duration',
      field: 'duration',
      type: 'numeric',
      render: rowData => `${rowData.duration} minute`,
    },
    {
      title: 'Category',
      field: 'categoryId',
      lookup: categoriesOptions
    },
  ];

  const categoriesColumns = [
    {
      title: 'Title',
      field: 'title',
    },
    {
      title: 'Order',
      field: 'order',
      type: 'numeric',
    },
  ];

  return (
    <div className={classes.root}>
      <Grid
        container
        spacing={2}
      >
        <Grid item xs={12} md={4}>
          <MaterialTable
            title="Categories list"
            columns={categoriesColumns}
            data={categories.map(item => ({...item}))}
            icons={{
              Check: CheckIcon,
              Export: ExportIcon,
              Add: AddIcon,
              Edit: EditIcon,
              Delete: CancelIcon,
              Search: SearchIcon,
              Clear: CancelIcon,
              FirstPage: FirstPageIcon,
              NextPage: NavigateNextIcon,
              LastPage: LastPageIcon,
              PreviousPage: NavigateBeforeIcon,
              ResetSearch: CancelIcon,
              SortArrow: ExpandMoreIcon,
            }}
            options={{
              exportButton: true,
              sorting: true,
              search: false,
              addRowPosition: 'first'
            }}
            isLoading={loading}
            editable={{
              onRowAdd: newData => addCategory(newData),
              onRowUpdate: (newData, oldData) => updateCategory(newData),
              onRowDelete: oldData => deleteCategory(oldData._id),
            }}
          />
        </Grid>
        <Grid item xs={12} md={8}>
          <MaterialTable
            title="Service list"
            isLoading={loading}
            options={{
              exportButton: true,
              sorting: true,
              addRowPosition: 'first'
            }}
            icons={{
              Check: CheckIcon,
              Export: ExportIcon,
              Add: AddIcon,
              Edit: EditIcon,
              Delete: CancelIcon,
              Search: SearchIcon,
              Clear: CancelIcon,
              FirstPage: FirstPageIcon,
              NextPage: NavigateNextIcon,
              LastPage: LastPageIcon,
              PreviousPage: NavigateBeforeIcon,
              ResetSearch: CancelIcon,
              SortArrow: ExpandMoreIcon,
            }}
            columns={columns}
            data={prices.map(item => ({...item}))}
            editable={{
              onRowAdd: newData => handleAdd(newData),
              onRowUpdate: (newData, oldData) => handleUpdate(newData),
              onRowDelete: oldData => handleDelete(oldData._id),
            }}
          />
        </Grid>
      </Grid>
    </div>
  );
}

export default Configs;
