import React from 'react';

import FormControlLabel from '@material-ui/core/FormControlLabel';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const paymentInfo = [
  {
    value: '',
    label: 'bitte angeben',
  },
  {
    value: 1,
    label: 'Cash',
  },
  {
    value: 2,
    label: 'EC',
  },
  {
    value: 3,
    label: 'Credit Card',
  },
];

const useStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(2),
    padding: theme.spacing(2),
  },
  topMargin: {
    marginTop: theme.spacing(1),
  },
  titleMargin: {
    marginBottom: theme.spacing(1),
  },
}));

function PaymentInfo({ errors, appointment, handleChange }) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Typography variant='h6' className={classes.titleMargin}>
        Payment info
      </Typography>
      <Grid container spacing={2}>
        <Grid item xs={12} md={2}>
          <TextField
            error={errors.paymentInfo}
            helperText={errors.paymentInfo && 'Field required'}
            select
            fullWidth
            label="Behandlungsgutschein"
            value={appointment.paymentInfo}
            onChange={e => handleChange('paymentInfo', e.target.value)}
          >
            {paymentInfo.map(option => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item xs={12} md={2}>
          <TextField
            error={errors.total}
            helperText={errors.total && 'Field required'}
            label='Total'
            type='number'
            value={appointment.total}
            onChange={e => handleChange('total', e.target.value)}
          />
        </Grid>
        <Grid item xs={12} md={5}>
          <TextField
            error={errors.note}
            helperText={errors.note && 'Field required'}
            className={classes.topMargin}
            fullWidth
            label="Anmerkung"
            multiline
            rows="4"
            variant="outlined"
            value={appointment.note}
            onChange={e => handleChange('note', e.target.value)}
          />
        </Grid>
        <Grid item xs={12} md={3}>
          <FormControlLabel
            control={<Checkbox/>}
            label="Anmerkung ist besonders wichtig"
            value={appointment.noteIsImportant}
            checked={appointment.noteIsImportant}
            onChange={e => handleChange('noteIsImportant', e.target.checked)}
          />
        </Grid>
      </Grid>
    </Paper>
  );
}

export default React.memo(PaymentInfo);
