import React, { useState, useCallback } from 'react';
import MaterialTable from 'material-table';

import { makeStyles } from '@material-ui/styles';

import ListItemText from '@material-ui/core/ListItemText';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import Checkbox from '@material-ui/core/Checkbox';
import Select from '@material-ui/core/Select';
import Input from '@material-ui/core/Input';
import Chip from '@material-ui/core/Chip';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExportIcon from '@material-ui/icons/CloudDownload';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import LastPageIcon from '@material-ui/icons/LastPage';
import CancelIcon from '@material-ui/icons/Cancel';
import SearchIcon from '@material-ui/icons/Search';
import CheckIcon from '@material-ui/icons/Check';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2),
  },
  content: {
    marginTop: theme.spacing(2)
  },
  loading: {
    display: 'flex',
    height: '100%',
    flexGrow: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    maxWidth: 300,
  },
  textField: {
    padding: theme.spacing(0),
  },
  list: {
    backgroundColor: theme.palette.background.paper,
  },
  hover: {
    '&:hover': {
      backgroundColor: theme.palette.primary.light,
    }
  }
}));

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const Workers = () => {
  const classes = useStyles();
  const { state: { workers, prices }, actions: { addWorker, updateWorker, deleteWorker }} = useStore();

  const [loading, setLoading] = useState(false);

  const handleSave = useCallback(data => {
    setLoading(true);

    if (data._id) {
      return updateWorker({ ...data }).then(() => {
        setLoading(false);
      });
    } else {
      return addWorker(data).then(() => {
        setLoading(false);
      });
    }
  }, [addWorker, updateWorker]);

  const handleDelete = useCallback(id => {
    setLoading(false);
    return deleteWorker(id).then(() => setLoading(false));
  }, [deleteWorker]);

  const serviceOptions = {};

  prices.forEach(item => serviceOptions[item._id] = item.title);

  const columns = [
    {
      title: 'Name',
      field: 'name',
    },
    {
      title: 'Service',
      field: 'serviceIds',
      lookup: serviceOptions,
      editComponent: ({ onChange, value = [] }) => (
        <FormControl className={classes.formControl}>
          <InputLabel id="demo-mutiple-checkbox-label">Services</InputLabel>
          <Select
            labelId="demo-mutiple-checkbox-label"
            id="demo-mutiple-checkbox"
            multiple
            value={value}
            onChange={e => onChange(e.target.value)}
            input={<Input />}
            renderValue={selected => prices.filter(price => selected.includes(price._id)).map(price => price.title).join(', ')}
            MenuProps={MenuProps}
          >
            {prices.map(price => (
              <MenuItem key={price._id} value={price._id}>
                <Checkbox checked={value.includes(price._id)} />
                <ListItemText primary={price.title} />
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      ),
      render: rowData => rowData.serviceIds.map(item => {
        const price = prices.find(p => p._id === item);
        return price && <Chip label={price.title}/>;
      })
    }
  ];

  return (
    <div className={classes.root}>
      <MaterialTable
        title="Worker list"
        columns={columns}
        data={workers.map(item => ({ ...item }))}
        icons={{
          Check: CheckIcon,
          Export: ExportIcon,
          Add: AddIcon,
          Edit: EditIcon,
          Delete: CancelIcon,
          Search: SearchIcon,
          Clear: CancelIcon,
          FirstPage: FirstPageIcon,
          NextPage: NavigateNextIcon,
          LastPage: LastPageIcon,
          PreviousPage: NavigateBeforeIcon,
          ResetSearch: CancelIcon,
          SortArrow: ExpandMoreIcon,
        }}
        options={{
          exportButton: true,
          sorting: true,
          search: false,
          addRowPosition: 'first'
        }}
        isLoading={loading}
        editable={{
          onRowAdd: newData => handleSave(newData),
          onRowUpdate: (newData, oldData) => handleSave(newData),
          onRowDelete: oldData => handleDelete(oldData._id),
        }}
      />
    </div>
  );
};

export default Workers;
