import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';

import { makeStyles, useTheme } from '@material-ui/styles';

import { useMediaQuery, CircularProgress } from '@material-ui/core';

import { useStore } from '../../store/DataProvider';

import Sidebar from './components/Sidebar';
import Topbar from './components/Topbar';
import Footer from './components/Footer';

import SignIn from '../SignIn';

const useStyles = makeStyles(theme => ({
  busyRoot: {
    display: 'flex',
    height: '100%',
    alignItems: 'center',
    justifyContent: 'center',
    '& > * + *': {
      marginLeft: theme.spacing(2),
    },
  },
  root: {
    paddingTop: 56,
    height: '100%',
    [theme.breakpoints.up('sm')]: {
      paddingTop: 64
    }
  },
  rootMinimal: {
    height: '100%'
  },
  shiftContent: {
    paddingLeft: 240
  },
  content: {
    height: '100%'
  }
}));

const App = props => {
  //localStorage.removeItem('user');
  const { children } = props;
  const { state: { user }, actions: { getSliders, getTexts, checkAuth, getServices, getCategories, getPrices, getWorkers, getClients, getAppointments } } = useStore();

  const classes = useStyles();
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up('lg'), {
    defaultMatches: true
  });

  const [openSidebar, setOpenSidebar] = useState(false);
  const [loading, setLoading] = useState(true);

  const handleSidebarOpen = () => {
    setOpenSidebar(true);
  };

  const handleSidebarClose = () => {
    setOpenSidebar(false);
  };

  const shouldOpenSidebar = isDesktop ? true : openSidebar;

  useEffect(() => {
    setLoading(true);
    if (user) {
      Promise.all([getSliders(), getTexts(), getServices(), getCategories(), getPrices(), getWorkers(), getClients(), getAppointments()]).then(() => setLoading(false));
    } else {
      checkAuth().then(() => {
        setLoading(false);
      })
    }
  }, [user, getSliders, getTexts, checkAuth, getServices, getCategories, getPrices, getWorkers, getClients, getAppointments]);

  return (
    <div
      className={clsx({
        [classes.root]: user,
        [classes.rootMinimal]: !user,
        [classes.shiftContent]: isDesktop && user
      })}
    >
      { loading ?  <div className={classes.busyRoot}>
        <CircularProgress color="secondary" />
      </div> : user ? <>
        <Topbar onSidebarOpen={handleSidebarOpen} />
        <Sidebar
          onClose={handleSidebarClose}
          open={shouldOpenSidebar}
          variant={isDesktop ? 'persistent' : 'temporary'}
        />
        <main className={classes.content}>
          {children}
          <Footer />
        </main>
      </> : <main className={classes.content}><SignIn/></main>}
    </div>
  );
};

App.propTypes = {
  children: PropTypes.node
};

export default App;
