import React, {useMemo} from 'react';

import Typography from '@material-ui/core/Typography';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Checkbox from '@material-ui/core/Checkbox';
import { makeStyles } from '@material-ui/styles';
import Table from '@material-ui/core/Table';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(2),
    padding: theme.spacing(2),
  },
  titleMargin: {
    marginBottom: theme.spacing(1),
  },
  table: {
    minWidth: 650,
  },
}));

function ServiceList({ errors, appointment, categories, prices, handleServiceAdd }) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Typography variant='h6' className={classes.titleMargin}>
        Service lists {errors.serviceIds && <Typography variant='h5' color='error'>Minimum 1 service must be selected</Typography>}
      </Typography>
      {categories.map(category => (
        <Table className={classes.table} key={category._id} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell>{category.title}</TableCell>
              <TableCell align="left">In Sale </TableCell>
              <TableCell align="left">Duration</TableCell>
              <TableCell align="left">Price</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {prices.filter(price => price.categoryId === category._id).map(price => (
              <TableRow key={price._id}>
                <TableCell component="th" scope="row">
                  <Checkbox
                    checked={appointment.serviceIds.includes(price._id)}
                    onChange={(e, checked) => handleServiceAdd(price._id, checked)}
                  />
                  {price.title}
                </TableCell>
                <TableCell align="left">Sale: {price.sale ? 'Yes' : 'No'}</TableCell>
                <TableCell align="left">{`${Math.floor(125 / 60)}:${145 % 60}`}</TableCell>
                <TableCell align="left"> {price.price}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      ))}
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>Total</TableCell>
            <TableCell align="right"> </TableCell>
            <TableCell align="right"> </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          <TableRow>
            <TableCell colSpan={3} align="right">Subtotal:  {appointment.subTotal}</TableCell>
          </TableRow>
          <TableRow>
            <TableCell colSpan={3} align="right">Total:  {appointment.total}</TableCell>
          </TableRow>
        </TableBody>
      </Table>
    </Paper>
  );
}

export default React.memo(ServiceList);
