import React, { useState, useCallback } from 'react';
import { MuiPickersUtilsProvider, DatePicker } from '@material-ui/pickers';
import PerfectScrollbar from 'react-perfect-scrollbar';
import MaterialTable from 'material-table';
import DayjsUtils from '@date-io/dayjs';
import dayjs from 'dayjs';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/styles';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExportIcon from '@material-ui/icons/CloudDownload';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import LastPageIcon from '@material-ui/icons/LastPage';
import FilterIcon from '@material-ui/icons/FilterList';
import CancelIcon from '@material-ui/icons/Cancel';
import SearchIcon from '@material-ui/icons/Search';
import CheckIcon from '@material-ui/icons/Check';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';

const defaultClient = {
  name: '',
  lastName: '',
  email: '',
  phone: '',
  city: '',
  post: '',
  address: '',
  homeNumber: '',
  additionalAddress: '',
  birthday: '',
};

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2)
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
  content: {
    padding: 0,
  },
  nameContainer: {
    display: 'flex',
    alignItems: 'center'
  },
  avatar: {
    marginRight: theme.spacing(2)
  },
  actions: {
    justifyContent: 'flex-end'
  },
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
    width: '100%',
  }
}));

function Clients() {
  const classes = useStyles();
  const {
    state: {
      clients,
      workers
    },
    actions: {
      updateClient,
      addClient,
    }
  } = useStore();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);

  const checkValidation = useCallback(data => {
    const validatableData = { ...defaultClient, ...data };
    let isValid = true;

    for (const key in validatableData) {
      if (validatableData.hasOwnProperty(key) && key !== 'note') {

        if (!validatableData[key]) {
          isValid = false;
        }
      }
    }

    return isValid;
  }, []);

  const handleAdd = useCallback(data => {
    setLoading(true);

    if (clients.find(item => item.email === data.email)) {
      alert('Client with this email exists');
      setLoading(false);
      return new Promise((res, rej) => rej());
    }

    if (clients.find(item => item.phone === data.phone)) {
      alert('Client with this phone number exists');
      setLoading(false);
      return new Promise((res, rej) => rej());
    }

    if (!checkValidation(data)) {
      setLoading(false);
      setError(true);
      return new Promise((res, rej) => rej());
    }

    return addClient(data).then(() => {
      setLoading(false);
      setError(false);
    });
  }, [addClient]);

  const handleUpdate = useCallback(data => {
    setLoading(true);

    if (clients.find(item => item.email === data.email && item._id !== data._id)) {
      alert('Client with this email exists');
      setLoading(false);
      return new Promise((res, rej) => rej());
    }

    if (clients.find(item => item.phone === data.phone && item._id !== data._id)) {
      alert('Client with this phone number exists');
      setLoading(false);
      return new Promise((res, rej) => rej());
    }

    if (!checkValidation(data)) {
      setLoading(false);
      setError(true);

      return new Promise((res, rej) => rej());
    }

    return updateClient(data).then(() => {
      setLoading(false);
      setError(false);
    });
  }, [updateClient]);

  const workerLookup = { 0: 'Not matter' };

  workers.forEach(worker => workerLookup[worker._id] = worker.name);

  const columns = [
    {
      cellStyle: { minWidth: 150 },
      title: 'Name',
      field: 'name',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Last name',
      field: 'lastName',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Phone',
      field: 'phone',
      type: 'numeric',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Email',
      field: 'email',
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Note',
      field: 'note',
      filtering: false,
      editComponent: props => (
        <TextField
          label="Note"
          multiline
          rowsMax="10"
          value={props.value}
          onChange={e => props.onChange(e.target.value)}
        />
      )
    },
    {
      cellStyle: { minWidth: 250 },
      title: 'Address',
      field: 'address',
      filtering: false,
      render: rowData => (
        <span>
          <p><b>City:</b> {rowData.city}</p>
          <p><b>Post:</b> {rowData.post}</p>
          <p><b>Address:</b> {rowData.address}</p>
          <p><b>Home number:</b> {rowData.homeNumber}</p>
          <p><b>Additional address:</b> {rowData.additionalAddress}</p>
        </span>
      ),
      editComponent: ({ rowData, onRowDataChange }) => (
        <>
          <TextField
            label="City"
            value={rowData.city}
            onChange={e => onRowDataChange({ ...rowData, city: e.target.value })}
          />
          <TextField
            label="Post"
            type='number'
            value={rowData.post}
            onChange={e => onRowDataChange({ ...rowData, post: e.target.value })}
          />
          <TextField
            label="Address"
            value={rowData.address}
            onChange={e => onRowDataChange({ ...rowData, address: e.target.value })}
          />
          <TextField
            label="Home Number"
            value={rowData.homeNumber}
            onChange={e => onRowDataChange({ ...rowData, homeNumber: e.target.value })}
          />
          <TextField
            label="Additional Address"
            value={rowData.additionalAddress}
            onChange={e => onRowDataChange({ ...rowData, additionalAddress: e.target.value })}
          />
        </>
      )
    },
    {
      cellStyle: { minWidth: 150 },
      title: 'Birthday',
      field: 'birthday',
      filtering: false,
      render: rowData => rowData.birthday ? dayjs(rowData.birthday).format('MMMM DD, YYYY') : '',
      editComponent: props => (
        <MuiPickersUtilsProvider utils={DayjsUtils}>
          <DatePicker
            label='Birthday'
            value={dayjs(props.value)}
            format='DD/MM/YYYY'
            onChange={date => props.onChange(date.toDate())}
          />
        </MuiPickersUtilsProvider>
      ),
    },
    {
      cellStyle: { minWidth: 150 },
      type: 'date',
      title: 'Created At',
      field: 'date',
      editable: 'never',
      filtering: false,
      render: rowData => rowData && dayjs(rowData.date).format('MMMM DD, YYYY'),
    },
  ];

  return (
    <div className={classes.root}>
      <PerfectScrollbar>
        <MaterialTable
          isLoading={loading}
          title={<Typography variant='h6'>Appointments list {error && <span style={{ color: 'red' }}>Please fill required fields</span>}</Typography>}
          columns={columns}
          data={clients.map(item => ({...item}))}
          icons={{
            Check: CheckIcon,
            Export: ExportIcon,
            Add: AddIcon,
            Filter: FilterIcon,
            Edit: EditIcon,
            Delete: CancelIcon,
            Search: SearchIcon,
            Clear: CancelIcon,
            FirstPage: FirstPageIcon,
            NextPage: NavigateNextIcon,
            LastPage: LastPageIcon,
            PreviousPage: NavigateBeforeIcon,
            ResetSearch: CancelIcon,
            SortArrow: ExpandMoreIcon,
          }}
          options={{
            exportButton: true,
            sorting: true,
            addRowPosition: 'first',
            filtering: true,
          }}
          editable={{
            onRowAdd: handleAdd,
            onRowUpdate: (newData, oldData) => handleUpdate(newData),
          }}
        />
      </PerfectScrollbar>
    </div>
  );
}

export default Clients;
