import React from 'react';
import dayjs from 'dayjs';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(2),
    padding: theme.spacing(2),
  },
  titleMargin: {
    marginBottom: theme.spacing(1),
  },
}));

function BookingInfo({ errors, appointment, handleChange, categories, workers }) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Typography variant='h6' className={classes.titleMargin}>
        Booking information
      </Typography>
      <Grid container spacing={2}>
        <Grid item xs={12} md={2}>
          <TextField
            label='Datum'
            disabled
            value={dayjs(appointment.startDate).format('DD.MM.YYYY')}
          />
        </Grid>
        <Grid item xs={12} md={2}>
          <TextField
            label='Zeit'
            disabled
            value={dayjs(appointment.startDate).format('HH:mm')}
          />
        </Grid>
        <Grid item xs={12} md={2}>
          <TextField
            error={errors.duration}
            helperText={errors.duration && 'Field required'}
            label='Dauer (minute)'
            type='number'
            value={appointment.duration}
            onChange={e => handleChange('duration', e.target.value)}
          />
        </Grid>
        <Grid item xs={12} md={3}>
          <TextField
            error={errors.worker}
            helperText={errors.worker && 'Field required'}
            select
            fullWidth
            label="Raum"
            value={appointment.worker}
            onChange={e => handleChange('worker', e.target.value)}
          >
            {workers.map(option => (
              <MenuItem key={option._id} value={option._id}>
                {option.name}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
        <Grid item xs={12} md={3}>
          <TextField
            error={errors.category}
            helperText={errors.category && 'Field required'}
            select
            fullWidth
            label="Typ"
            value={appointment.category}
            onChange={e => handleChange('category', e.target.value)}
          >
            {categories.map(option => (
              <MenuItem key={option._id} value={option._id}>
                {option.title}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
      </Grid>
    </Paper>
  )
}

export default React.memo(BookingInfo);
