import React, {useState} from 'react';
import clsx from 'clsx';
import dayjs from 'dayjs';
import { makeStyles } from '@material-ui/styles';
import { Grid } from '@material-ui/core';

import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import { red } from '@material-ui/core/colors';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import EditIcon from '@material-ui/icons/Edit';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import RemoveIcon from '@material-ui/icons/Delete';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  },
  card: {
    maxWidth: 345,
  },
  cardFullWidth: {
    width: '100%',
  },
  media: {
    height: 0,
    paddingTop: '56.25%', // 16:9
  },
  expand: {
    transform: 'rotate(0deg)',
    marginLeft: 'auto',
    transition: theme.transitions.create('transform', {
      duration: theme.transitions.duration.shortest,
    }),
  },
  expandOpen: {
    transform: 'rotate(180deg)',
  },
  avatar: {
    backgroundColor: red[500],
  },
}));

function CardItem({ title, description, image, points, date, onEdit, onDelete, headerAction }) {
  const classes = useStyles();

  const [expanded, setExpanded] = useState(false);

  return (
    <div className={classes.root}>
      <Grid
        container
        spacing={4}
      >
        <Card className={date ? classes.card : classes.cardFullWidth}>
          <CardHeader
            action={headerAction}
            title={title}
            subheader={date ? dayjs(date).format('MMMM DD, YYYY') : null}
          />
          <CardMedia
            className={classes.media}
            image={image}
            title={title}
          />
          <CardContent>
            <Typography variant="body2" color="textSecondary" component="p">
              {description}
            </Typography>
          </CardContent>
          <CardActions disableSpacing>
            <IconButton aria-label="edit item" onClick={onEdit}>
              <EditIcon />
            </IconButton>
            <IconButton aria-label="delete item" onClick={onDelete}>
              <RemoveIcon />
            </IconButton>
            {points && <IconButton
              className={clsx(classes.expand, {
                [classes.expandOpen]: expanded,
              })}
              onClick={() => setExpanded(v => !v)}
              aria-expanded={expanded}
              aria-label="show more"
            >
              <ExpandMoreIcon />
            </IconButton>}
          </CardActions>
          <Collapse in={expanded} timeout="auto" unmountOnExit>
            {points && points.map((item, index) => {
              return (
                <CardContent key={index}>
                  <Typography paragraph>{item.title}: </Typography>
                  <Typography paragraph>{item.description}</Typography>
                </CardContent>
              );
            })}
          </Collapse>
        </Card>
      </Grid>
    </div>
  );
}

export default CardItem;
