import React, { useEffect, useState, useCallback } from 'react';
import { ContentState, convertFromHTML, convertToRaw, EditorState } from 'draft-js';
import { Editor as DraftEditor } from 'react-draft-wysiwyg';
import { DropzoneArea } from 'material-ui-dropzone';
import MaterialTable from 'material-table';
import draftToHtml from 'draftjs-to-html';

import { makeStyles } from '@material-ui/styles';

import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';

import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore';
import NavigateNextIcon from '@material-ui/icons/NavigateNext';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ExportIcon from '@material-ui/icons/CloudDownload';
import FirstPageIcon from '@material-ui/icons/FirstPage';
import LastPageIcon from '@material-ui/icons/LastPage';
import CancelIcon from '@material-ui/icons/Cancel';
import SearchIcon from '@material-ui/icons/Search';
import CheckIcon from '@material-ui/icons/Check';
import EditIcon from '@material-ui/icons/Edit';
import AddIcon from '@material-ui/icons/Add';

import { useStore } from '../../store/DataProvider';

import Editor from './components/editor';

import { defaultToolbarConfig } from './configToolbar';

import 'react-draft-wysiwyg/dist/react-draft-wysiwyg.css';
import './index.scss';

const defaultService = {
  title: '',
  subTitle: '',
  order: 0,
  alias: '',
  description: '',
  image: '',
  disabled: false,
  points: [],
  prices: [],
};

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(2)
  },
  margin: {
    marginTop: theme.spacing(4),
  },
  spacer: {
    display: 'flex',
    flex: 1,
    flexGrow: 1,
  },
  grow: {
    flexGrow: 1,
  },
  dropzoneContainer: {
    '& .MuiGrid-container': {
      alignItems: 'center',
      justifyContent: 'center',
    }
  },
  fab: {
    position: 'absolute',
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
}));

const convertToDraft = (draft) => {
  const state = draft ? ContentState.createFromBlockArray(
    convertFromHTML(draft).contentBlocks,
    convertFromHTML(draft).entityMap
  ) : '';

  if (state) {
    return EditorState.createWithContent(state);
  } else {
    return EditorState.createEmpty();
  }
};

function Detailed(props) {
  const classes = useStyles();
  const { state: { services }, actions: {
    addService,
    updateService,
    addPoint,
    deletePoint,
    updatePoint,
    addPriceCategory,
    deletePriceCategory,
    updatePriceCategory,
    addPriceCategoryData,
    deletePriceCategoryData,
    updatePriceCategoryData,
  } } = useStore();
  const selectedId = props.match.params.id;

  const [service, setService] = useState({ ...defaultService });
  const [loading, setLoading] = useState(false);
  const [errors, setErrors] = useState({});
  const [editorState, setEditorState] = useState(
    () => {
      const state = service.description ? ContentState.createFromBlockArray(
        convertFromHTML(service.description).contentBlocks,
        convertFromHTML(service.description).entityMap
      ) : '';

      if (state) {
        return EditorState.createWithContent(state);
      } else {
        return EditorState.createEmpty();
      }
    }
  );

  const selected = services.find(item => item._id === selectedId) || {};

  const handleChange = useCallback((key, value) => {
    setService(data => ({ ...data, [key]: value }));
  }, []);

  const handleEditorChange = useCallback(editor => {
    setEditorState(editor);
  }, []);

  const handleSave = useCallback(() => {
    let error = false;

    if (!service.title || !service.alias || !service.subTitle || service.order === '' || draftToHtml(convertToRaw(editorState.getCurrentContent())).trim() === '<p></p>') {
      setErrors({
        title: !service.title,
        alias: !service.alias,
        subTitle: !service.subTitle,
        order: true,
        description: draftToHtml(convertToRaw(editorState.getCurrentContent())).trim() === '<p></p>',
      });
      error = true;
    }

    if (service._id) {
      !error && updateService({
        ...service,
        description: draftToHtml(convertToRaw(editorState.getCurrentContent()))
      }).then(() => props.history.push('/services'));
    } else {
      !error && addService({
        ...service,
        description: draftToHtml(convertToRaw(editorState.getCurrentContent()))
      }).then(() => props.history.push('/services'));
    }
  }, [props.history, updateService, addService, service, editorState]);

  const handlePointSave = useCallback(data => {
    setLoading(true);
    return addPoint({
      ...data,
      _id: service._id,
    }).then(() => setLoading(false));
  }, [addPoint, service._id]);

  const handlePointUpdate = useCallback(data => {
    setLoading(true);
    return updatePoint({
      ...data,
      serviceId: service._id,
    }).then(() => setLoading(false));
  }, [updatePoint, service._id]);

  const handlePointDelete = useCallback(id => {
    setLoading(true);
    return deletePoint({ serviceId: service._id, _id: id }).then(() => setLoading(false));
  }, [deletePoint, service._id]);

  const handlePriceCategorySave = useCallback(data => {
    setLoading(true);
    return addPriceCategory({ serviceId: service._id, ...data }).then(() => setLoading(false));
  }, [addPriceCategory, service._id]);

  const handlePriceCategoryUpdate = useCallback(data => {
    setLoading(true);
    return updatePriceCategory({ serviceId: service._id, ...data }).then(() => setLoading(false));
  }, [updatePriceCategory, service._id]);

  const handlePriceCategoryDelete = useCallback(id => {
    setLoading(true);
    return deletePriceCategory({ serviceId: service._id, _id: id }).then(() => setLoading(false));
  }, [deletePriceCategory, service._id]);

  const handlePriceCategoryDataAdd = useCallback(data => {
    setLoading(true);
    return addPriceCategoryData({ serviceId: service._id, ...data }).then(() => setLoading(false));
  }, [addPriceCategoryData, service._id]);

  const handlePriceCategoryDataDelete = useCallback((categoryId, priceId) => {
    setLoading(true);
    return deletePriceCategoryData({ serviceId: service._id, categoryId, priceId }).then(() => setLoading(false));
  }, [deletePriceCategoryData, service._id]);

  const handlePriceCategoryDataUpdate = useCallback(data => {
    setLoading(true);
    return updatePriceCategoryData({ serviceId: service._id, ...data }).then(() => setLoading(false));
  }, [updatePriceCategoryData, service._id]);

  useEffect(() => {
    if (selected._id) {
      setService(selected);
      const state = selected.description ? ContentState.createFromBlockArray(
        convertFromHTML(selected.description).contentBlocks,
        convertFromHTML(selected.description).entityMap
      ) : '';

      if (state) {
        setEditorState(EditorState.createWithContent(state));
      } else {
        setEditorState(EditorState.createEmpty());
      }
    }
  }, [selected]);

  const pointColumns = [
    {
      title: 'Title',
      field: 'title',
    },
    {
      title: 'Order',
      field: 'order',
      type: 'numeric',
    },
    {
      title: 'Description',
      field: 'description',
      editComponent: props => (
        <Editor
          data={props.value}
          onChange={props.onChange}
        />
      ),
      render: rowData => <div style={{
        textOverflow: 'ellipsis',
        whiteSpace: 'nowrap',
        overflow: 'hidden',
        maxWidth: 900,
      }}>
        {rowData.description}
      </div>
    }
  ];

  const priceCategoryColumns = [
    {
      title: 'Title',
      field: 'title',
    },
    {
      title: 'Order',
      field: 'order',
      type: 'numeric',
    }
  ];

  return (
    <div className={classes.root}>
      <Paper className={classes.root}>
        <Grid
        container
        spacing={2}
      >
        <Grid item xs={12} className={classes.spacer}>
          <Typography className={classes.grow} variant='h4'>{service.title || 'New service'} </Typography>
          <Button onClick={handleSave} variant="contained" color="primary">Save</Button>
        </Grid>
        <Grid item xs={12}>
          <form noValidate autoComplete="off">
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              <Grid item xs={12} md={6}>
                <TextField
                  error={errors['title']}
                  helperText={errors['title'] ? "Incorrect entry" : ""}
                  fullWidth
                  label="Title"
                  onChange={e => handleChange('title', e.target.value)}
                  value={service.title}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  error={errors['alias']}
                  helperText={errors['alias'] ? "Incorrect entry" : ""}
                  fullWidth
                  label="Alias"
                  onChange={e => handleChange('alias', e.target.value)}
                  value={service.alias}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  error={errors['subTitle']}
                  helperText={errors['subTitle'] ? "Incorrect entry" : ""}
                  fullWidth
                  label="Sub title"
                  onChange={e => handleChange('subTitle', e.target.value)}
                  value={service.subTitle}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField
                  error={errors['order']}
                  helperText={errors['order'] ? "Incorrect entry" : ""}
                  fullWidth
                  label="Order"
                  type="number"
                  onChange={e => handleChange('order', e.target.value)}
                  value={service.order}
                />
              </Grid>
            </Grid>
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              <Grid item xs={12}>
                <DraftEditor
                  editorStyle={{ height: 200, overflow: 'scroll', padding: 5, border: errors['description'] ? '1px solid red' : 'none' }}
                  editorState={editorState}
                  onEditorStateChange={handleEditorChange}
                  toolbar={defaultToolbarConfig}
                />
              </Grid>
              <Grid item xs={1} md={2} lg={3}> </Grid>
              <Grid item xs={12} md={8} lg={6}>
                <DropzoneArea
                  dropzoneClass={classes.dropzoneContainer}
                  onChange={e => handleChange('image', e[0])}
                  acceptedFiles={['image/*']}
                  initialFiles={service.image ? [`/${service.image}`] : []}
                  filesLimit={1}
                />
              </Grid>
              <Grid item xs={1} md={2} lg={3}> </Grid>
            </Grid>
            <Grid
              className={classes.margin}
              container
              spacing={2}
            >
              <Grid item xs={12}>
                <MaterialTable
                  title='Points list'
                  columns={pointColumns}
                  data={service.points.map(item => ({ ...item }))}
                  icons={{
                    Check: CheckIcon,
                    Export: ExportIcon,
                    Add: AddIcon,
                    Edit: EditIcon,
                    Delete: CancelIcon,
                    Search: SearchIcon,
                    Clear: CancelIcon,
                    FirstPage: FirstPageIcon,
                    NextPage: NavigateNextIcon,
                    LastPage: LastPageIcon,
                    PreviousPage: NavigateBeforeIcon,
                    ResetSearch: CancelIcon,
                    SortArrow: ExpandMoreIcon,
                  }}
                  options={{
                    exportButton: true,
                    sorting: true,
                    search: false,
                    addRowPosition: 'first'
                  }}
                  isLoading={loading}
                  editable={{
                    onRowAdd: newData => handlePointSave(newData),
                    onRowUpdate: (newData, oldData) => handlePointUpdate(newData),
                    onRowDelete: oldData => handlePointDelete(oldData._id),
                  }}
                />
              </Grid>
              <Grid item xs={12}>
                <MaterialTable
                  title='Points list'
                  columns={priceCategoryColumns}
                  data={service.prices.map(item => ({ ...item }))}
                  icons={{
                    Check: CheckIcon,
                    Export: ExportIcon,
                    Add: AddIcon,
                    Edit: EditIcon,
                    Delete: CancelIcon,
                    Search: SearchIcon,
                    Clear: CancelIcon,
                    FirstPage: FirstPageIcon,
                    NextPage: NavigateNextIcon,
                    LastPage: LastPageIcon,
                    PreviousPage: NavigateBeforeIcon,
                    ResetSearch: CancelIcon,
                    SortArrow: ExpandMoreIcon,
                    DetailPanel: ChevronRightIcon,
                  }}
                  options={{
                    exportButton: true,
                    sorting: true,
                    search: false,
                    addRowPosition: 'first'
                  }}
                  isLoading={loading}
                  editable={{
                    onRowAdd: newData => handlePriceCategorySave(newData),
                    onRowUpdate: (newData, oldData) => handlePriceCategoryUpdate(newData),
                    onRowDelete: oldData => handlePriceCategoryDelete(oldData._id),
                  }}
                  detailPanel={rowData => (
                    <MaterialTable
                      options={{
                        addRowPosition: 'first',
                        header: false,
                        grouping: false,
                        paging: false,
                        search: false,
                        toolbar: true,
                        showTitle: false,
                      }}
                      columns={[
                        {
                          title: 'Price',
                          field: 'price',
                        },
                        {
                          title: 'Order',
                          field: 'order',
                          type: 'numeric',
                        },
                        {
                          title: 'Description',
                          field: 'description',
                          editComponent: props => (
                            <Editor
                              data={props.value}
                              onChange={props.onChange}
                            />
                          ),
                          render: rowData => <div style={{
                            textOverflow: 'ellipsis',
                            whiteSpace: 'nowrap',
                            overflow: 'hidden',
                            maxWidth: 900,
                          }}>
                            {rowData.description}
                          </div>
                        }
                      ]}
                      icons={{
                        Check: CheckIcon,
                        Add: AddIcon,
                        Edit: EditIcon,
                        Delete: CancelIcon,
                        Clear: CancelIcon,
                      }}
                      data={rowData.data.map(item => ({ ...item }))}
                      editable={{
                        onRowAdd: newData => handlePriceCategoryDataAdd({ ...newData, categoryId: rowData._id }),
                        onRowUpdate: (newData, oldData) => handlePriceCategoryDataUpdate({ ...newData, categoryId: rowData._id }),
                        onRowDelete: oldData => handlePriceCategoryDataDelete(rowData._id, oldData._id),
                      }}
                    />
                  )}
                />
              </Grid>
            </Grid>
          </form>
        </Grid>
      </Grid>
      </Paper>
    </div>
  );
}

export default Detailed;
