import React, {useCallback, useEffect, useState} from 'react';
import dayjs from 'dayjs';
import Scheduler, { Resource, View, AppointmentDragging, Editing } from 'devextreme-react/scheduler';

import { makeStyles } from '@material-ui/core/styles';
import CardContent from '@material-ui/core/CardContent';
import CardHeader from '@material-ui/core/CardHeader';
import Typography from '@material-ui/core/Typography';
import Card from '@material-ui/core/Card';

import { useStore } from '../../store/DataProvider';

import './style.scss';
import CircularProgress from '@material-ui/core/CircularProgress';

const useStyles = makeStyles(() => ({
  root: {
    maxWidth: 345,
  },
}));

const currentDate = new Date();

const colors = [
  "#66bb6a",
  "#ff7043",
  "#5c6bc0",
  "#ef5350",
  "#d4e157",
  "#b71c1c",
  "#1a237e",
  "#1b5e20",
  "#bf360c",
  "#827717",
];

const views = [
  {
    type: 'day',
    name: 'Day',
  },
  {
    type: 'week',
    name: 'Week',
  },
  {
    type: 'month',
    name: 'Month',
  },
  {
    type: 'timelineDay',
    name: 'Day timeline'
  },
  {
    type: 'timelineWorkWeek',
    name: 'Week timeline'
  },
  {
    type: 'timelineMonth',
    name: 'Month timeline'
  }
];

const groups = ['worker'];

export default function Calendars({ history }) {
  const classes = useStyles();

  const { state: { workers, prices, appointments }, actions: { setSelectedAppointment, updateAppointment } } = useStore();

  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(appointments.map(item => ({
    ...item,
    clientId: { ...item.clientId },
    worker: item.worker || 0,
    text: `${item.clientId.name} ${item.clientId.lastName} - ${item.clientId.phone} - ${item.clientId.email}`,
    startDate: item.time,
    endDate: item.timeEnd,
  })));

  const handleDragEnd = useCallback(({ newData, ...event }) => {
    setLoading(true);

    const appointment = {
      ...newData,
      time: newData.startDate,
      timeEnd: newData.endDate,
      worker: newData.worker === 0 ? null : newData.worker,
      duration: dayjs(newData.endDate).diff(newData.startDate, 'minute'),
    };

    const newAppointmentIndex = data.findIndex(item => item._id === newData._id);
    const dataList = [ ...data ];
    dataList[newAppointmentIndex] = { ...appointment };

    setData(dataList);

    return updateAppointment(appointment).then(() => setLoading(false));
  }, [updateAppointment, data]);

  const workerData = workers.map((item, index) => ({ id: item._id, color: colors[index+1], text: item.name }));
  workerData.push({ id: 0, color: colors[0], text: 'Not matter' });

  useEffect(() => {
    setData(appointments.map(item => ({
      ...item,
      clientId: { ...item.clientId },
      worker: item.worker || 0,
      text: `${item.clientId.name} ${item.clientId.lastName} - ${item.clientId.phone} - ${item.clientId.email}`,
      startDate: item.time,
      endDate: item.timeEnd,
    })));
  }, [appointments]);

  return (
    <div id="scheduler">
      {loading && <div className='start-loading'>
        <div className={classes.busyRoot}>
          <CircularProgress color="secondary" />
        </div>
      </div>}
      <Scheduler
        shadeUntilCurrentTime
        showCurrentTimeIndicator
        dataSource={data}
        views={views}
        groups={groups}
        groupByDate={true}
        showAllDayPanel={false}
        defaultCurrentView="week"
        defaultCurrentDate={currentDate}
        startDayHour={10}
        endDayHour={19}
        cellDuration={15}
        onAppointmentUpdating={handleDragEnd}
        onAppointmentFormOpening={data => {
          setSelectedAppointment(data.appointmentData).then(() => {
            history.push(`/appointments/0`);
          });
          data.cancel = true;
          return data;
        }}
        onAppointmentDblClick={(data) => {
          setSelectedAppointment(data.appointmentData).then(() => {
            history.push(`/appointments/${data.appointmentData._id}`);
          });
          data.cancel = true;
          return data;
        }}
        firstDayOfWeek={1}
        appointmentTooltipComponent={({ data: { targetedAppointmentData: item }}) => (
          <Card className={classes.root}>
            <CardHeader
              title={`${item.clientId.name} ${item.clientId.lastName}`}
              subheader={`${dayjs(item.time).format('MMMM DD HH:mm')} - ${dayjs(item.timeEnd).format('HH:mm')}`}
            />
            <CardContent>
              <Typography variant="body1" color="textSecondary" component="p" style={{ whiteSpace: 'normal' }}>
                Services: {prices.filter(price => item.serviceIds.includes(price._id)).map(price => price.title).join(', ')}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                Email: {item.clientId.email}
              </Typography>
              <Typography variant="body2" color="textSecondary" component="p">
                Phone: {item.clientId.phone}
              </Typography>
            </CardContent>
          </Card>
        )}
        appointmentComponent={({ data: { targetedAppointmentData: item }}) => (
          <div>
            <p>{item.clientId.name} {item.clientId.lastName}</p>
            <p>Email: {item.clientId.email}</p>
            <p>Phone: {item.clientId.phone}</p>
            <p>{dayjs(item.time).format('HH:mm')} - {dayjs(item.timeEnd).format('HH:mm')}</p>
          </div>
        )}
        crossScrollingEnabled={true}>
        <Resource
          fieldExpr="worker"
          allowMultiple={false}
          dataSource={workerData}
          label="Worker"
        />
        <View
          type="week"
          name="Week"
        />
        <View
          type="month"
          name="Month"
        />
        <View
          type="timelineWeek"
          name="Timeline Week"
          groupOrientation="horizontal"
        />
        <AppointmentDragging

        />
      </Scheduler>
    </div>
  );
}
