import React, {useCallback, useEffect, useState} from 'react';
import draftToHtml from 'draftjs-to-html';
import { Editor as DraftEditor } from 'react-draft-wysiwyg';
import { ContentState, convertFromHTML, convertToRaw, EditorState } from 'draft-js';

import { defaultToolbarConfig } from '../../configToolbar';

export default function PointList({ data, onChange, error }) {

  const [editorState, setEditorState] = useState(
    () => {
      const state = data ? ContentState.createFromBlockArray(
        convertFromHTML(data).contentBlocks,
        convertFromHTML(data).entityMap
      ) : '';

      if (state) {
        return EditorState.createWithContent(state);
      } else {
        return EditorState.createEmpty();
      }
    }
  );

  const handleChange = useCallback(editor => {
    setEditorState(editor);
    onChange && onChange(draftToHtml(convertToRaw(editor.getCurrentContent())));
  }, [onChange]);

  return (
    <DraftEditor
      wrapperStyle={{ border: error ? '1px solid red' : 'none' }}
      editorStyle={{ height: 180, overflow: 'scroll', padding: 5 }}
      editorState={editorState}
      onEditorStateChange={handleChange}
      toolbar={defaultToolbarConfig}
    />
  );
}
