import React from 'react';
import dayjs from 'dayjs';
import { DatePicker, MuiPickersUtilsProvider } from '@material-ui/pickers';
import DayjsUtils from '@date-io/dayjs';

import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormControl from '@material-ui/core/FormControl';
import Typography from '@material-ui/core/Typography';
import RadioGroup from '@material-ui/core/RadioGroup';
import TextField from '@material-ui/core/TextField';
import Checkbox from '@material-ui/core/Checkbox';
import { makeStyles } from '@material-ui/styles';
import Paper from '@material-ui/core/Paper';
import Radio from '@material-ui/core/Radio';
import Grid from '@material-ui/core/Grid';
import MenuItem from '@material-ui/core/MenuItem';

const useStyles = makeStyles(theme => ({
  root: {
    margin: theme.spacing(2),
    padding: theme.spacing(2),
  },
  topMargin: {
    marginTop: theme.spacing(1),
  },
  titleMargin: {
    marginBottom: theme.spacing(1),
  },
}));

function ClientInfo({ clients, errors, older, client, appointment, handleChange, handleClientChange, selectClient }) {
  const classes = useStyles();

  return (
    <Paper className={classes.root}>
      <Typography variant='h6' className={classes.titleMargin}>
        Client info
      </Typography>
      <Grid container>
        <FormControl component="fieldset">
          <RadioGroup aria-label="gender" name="gender1" value='female' onChange={() => {}}>
            <Grid item xs={6}>
              <FormControlLabel
                value="new"
                checked={!older}
                disabled
                control={<Radio />}
                label="Neukunde"
              />
            </Grid>
            <Grid item xs={6}>
              <FormControlLabel
                value="old"
                checked={older}
                disabled
                control={<Radio />}
                label="Bestandskunde"
              />
            </Grid>
          </RadioGroup>
        </FormControl>
      </Grid>
      {!older &&  <Grid container>
        <Grid item xs={12} md={2}>
          <TextField
            select
            fullWidth
            label="Select user"
            value={client._id || ''}
            onChange={e => selectClient(e.target.value)}
          >
            <MenuItem value=''>
              New User
            </MenuItem>
            {clients.map(option => (
              <MenuItem key={option._id} value={option._id}>
                {option.name}
              </MenuItem>
            ))}
          </TextField>
        </Grid>
      </Grid>}
      <Grid container>
        <Grid item xs={12} md={2}>
          <TextField
            error={errors.name}
            helperText={errors.name && 'Field required'}
            label='Name'
            disabled={!older || client._id}
            type='text'
            value={client.name}
            onChange={e => handleClientChange('name', e.target.value)}
          />
        </Grid>
        <Grid item xs={12} md={2}>
          <TextField
            error={errors.phone}
            helperText={errors.phone && 'Field required'}
            label='Telefon'
            disabled={!older || client._id}
            type='number'
            value={client.phone}
            onChange={e => handleClientChange('phone', e.target.value)}
          />
        </Grid>
        <Grid item xs={12} md={2}>
          <TextField
            error={errors.lastName}
            helperText={errors.lastName && 'Field required'}
            label='Vorname'
            disabled={!older || client._id}
            type='text'
            value={client.lastName}
            onChange={e => handleClientChange('lastName', e.target.value)}
          />
        </Grid>
        {!older && !client._id && <>
          <Grid item xs={12} md={2}>
            <TextField
              error={errors.email}
              helperText={errors.email && 'Field required'}
              label='Email'
              type='text'
              value={client.email}
              onChange={e => handleClientChange('email', e.target.value)}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              error={errors.city}
              helperText={errors.city && 'Field required'}
              label="City"
              value={client.city}
              onChange={e => handleClientChange('city', e.target.value)}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              error={errors.post}
              helperText={errors.post && 'Field required'}
              label="Post"
              type='number'
              value={client.post}
              onChange={e => handleClientChange('post', e.target.value)}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              error={errors.address}
              helperText={errors.address && 'Field required'}
              label="Address"
              value={client.address}
              onChange={e => handleClientChange('address', e.target.value)}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              error={errors.homeNumber}
              helperText={errors.homeNumber && 'Field required'}
              label="Home Number"
              value={client.homeNumber}
              onChange={e => handleClientChange('homeNumber', e.target.value)}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              error={errors.additionalAddress}
              helperText={errors.additionalAddress && 'Field required'}
              label="Additional Address"
              value={client.additionalAddress}
              onChange={e => handleClientChange('additionalAddress', e.target.value)}
            />
          </Grid>
          <Grid item xs={12} md={2}>
            <MuiPickersUtilsProvider utils={DayjsUtils}>
              <DatePicker
                error={errors.birthday}
                helperText={errors.birthday && 'Field required'}
                label='Birthday'
                value={client.birthday ? dayjs(client.birthday) : null}
                format='DD/MM/YYYY'
                onChange={date => handleClientChange('birthday', date.toDate())}
              />
            </MuiPickersUtilsProvider>
          </Grid>
          <Grid item xs={12} md={2}>
            <TextField
              label="Note"
              multiline
              rowsMax="3"
              value={client.note}
              onChange={e => handleClientChange('note', e.target.value)}
            />
          </Grid>
          <Grid item xs={12} md={2}></Grid>
        </>}
        <Grid item xs={12} md={2}>
          <FormControlLabel
            control={<Checkbox />}
            label="Emails senden"
            value={appointment.emailSent}
            checked={appointment.emailSent}
            onChange={e => handleChange('emailSent', e.target.checked)}
          />
        </Grid>
        <Grid item xs={12} md={4}>
          <Grid container>
            <Grid item xs={12} md={6}>
              <FormControlLabel
                control={<Checkbox />}
                label="Laser-Kunde"
                value={appointment.laserKunde}
                checked={appointment.laserKunde}
                onChange={e => handleChange('laserKunde', e.target.checked)}
              />
            </Grid>
            <Grid item xs={12} md={6}>
              <FormControlLabel
                control={<Checkbox />}
                label="IPL-Kunde"
                value={appointment.IPLKunde}
                checked={appointment.IPLKunde}
                onChange={e => handleChange('IPLKunde', e.target.checked)}
              />
            </Grid>
          </Grid>
        </Grid>
      </Grid>
      <Grid container>
        <Grid item xs={12} className={classes.topMargin}>
          <TextField
            margin='dense'
            fullWidth
            label="Wichtige Hinweise"
            multiline
            rows="4"
            variant="outlined"
            value={appointment.importantInstructions}
            onChange={e => handleChange('importantInstructions', e.target.value)}
          />
        </Grid>
      </Grid>
    </Paper>
  );
}

export default React.memo(ClientInfo);
